
@import "../module_scss/font_customize";

$font_color: #333;
$btnSide_color: rgba(255, 255, 255, 0.219);
$logo_color: #ff7b1a;
$logo_color_hover: #ff791ab7;

$main_item_imgW: 240px;
$main_item_imgH: 153.4px;


*{
    font-family: "Microsoft YaHei";
    scroll-behavior: smooth;
}
body{
    background-color: #f8f8f8;
}

//Header
.header{
    z-index: 1;
    display: block;
    position: fixed;
    top: 0;
    width: 100%;
    height: 105px;
    background-color: rgba(255, 255, 255, 0.315);
    .logo{
        display: inline-block;
        background-image: url(../images/TTTTaiwan-V2.png);
        background-size: 100%;
        background-repeat: no-repeat;
        width: 95px;
        height: 95px;
        position: absolute;
        top: 5px;
        left: 26%;
    }
    .nav_item_container{
        position: sticky;
        width: 26%;
        margin-top: 25px;
        margin-left: 59%;
        display: flex;
        justify-content: space-between;

        .nav_item{
            display: inline-block;
            text-align: center;
            margin-right: 5%;
            margin-left: 5%;

            .a:link,:visited{
                @include font(3);

                text-decoration-line: none;
                color: $font_color;
            }
            .a{
                display: block;
                width: 97px;
                height: 80px;
            }
            .a:hover{
                color: #000;
            }

        }
    }
    .hover_item_container{
        width: 100%;
        position: sticky;
        display: flex;
        justify-content: space-between;

        .hover_nav{
            width: 40%;
            margin-left: 53%;
            height: 140px;
            background-color: #d8d8d8c4;
            text-align: center;
            border-radius: 20px;

            .hover_item{
                @include font(1);
                color: #623A28;
                text-align: center;
                font-weight: 850;
                line-height: 50px;
                cursor: pointer;
                display: inline-block;
                margin-right: 0.45vw;
                margin-left: 0.45vw;
                margin-top: 12px;
                width: 16%;
                height: 120px;
                background-size: 45%;
                background-position: 50% 70% ;
                background-repeat: no-repeat;
            }
            .hover_item:hover{
                color: #a7522b;
            }
            .line{
                display: inline-block;
                width: 2px;
                height: 80px;
                background-color: #ff791a8c;
                margin-left: 1px;
                color: #00000000;
            }
        }
    }
    .side_bar{
        position: relative;
        .filter_blur{
            width: 100vw;
            height: 100vh;
            background-color: #6d6d6d7a;
        }
        .bar_btn{
            position: absolute;
            right: 10px;
            top: 5px;
            cursor: pointer;
            display: none;
            i{
                font-size: 40px;
                color: #4141418c;
            }
        }
        .nav_left{
            width: 240px;
            height: 100%;
            position: absolute;
            top: 0;
            left: -100%;
            background-color: white;
            color: $font_color;
            transition: all 0.25s;
            ul{
                list-style-type: none;
                margin-bottom: 5px;
                padding: 0;
                li{
                    @include font(2);
                    padding-top: 10px;
                    padding-bottom: 10px;
                    color: $logo_color;
                    a:visited{
                        color: $logo_color;
                    }
                }
                
                .title{
                    position: relative;
                    margin-top: 30px;
                    background-color: #f0f0f0f0;
                    text-align: start;
                    cursor: pointer;
                    .title_a{
                        font-weight: 600;
                        padding-left: 40px;
                    }
                    ul{
                        display: none;
                        li{
                            padding-left: 80px;
                            a{
                                display: block;
                                width: 100%;
                                height: 100%;
                            }
                        }
                        li:hover{
                            background-color: #c4c4c4d7;
                        }
                    }
                }
                .title::before{
                    content: "";
                    width: 0;
                    height: 0;
                    position: absolute;
                    right: 25px;
                    top: 19px;
                    background-color: transparent;
                    border-top: 6px solid $logo_color;
                    border-bottom: 6px solid transparent;
                    border-right: 6px solid transparent;
                    border-left: 6px solid transparent;
                }
                .title:hover{
                    .title_a{
                        color: $logo_color_hover;
                    }
                }
            }
        }
    }
    
}
@media(max-width: 1200px){
    .header{
        height: 69px;

        .logo{
            width: 62px;
            height: 62px;
        }
        .nav_item_container{
            margin-top: 18px;

            .nav_item{

                .a:link,:visited{
                    font-size: 16px;
                }
                .a{
                    width: 85px;
                    height: 51px;
                }
            }
        }
        .hover_item_container{
            width: 100%;
            position: sticky;
            display: flex;
            justify-content: space-between;

            .hover_nav{

                .hover_item{
                    width: 80px;
                }
            }
        }
    }
}
@media(max-width: 1179px){
    .header{

        .nav_item_container{
            margin-left: 54%;
        }
        .hover_item_container{

            .hover_nav:nth-of-type(1){
                height: 233px;

                .hover_item{
                    margin-top: 0px;
                }
            }
        }
    }
}
@media(max-width: 922px){
    .header{

        .hover_item_container{

            .hover_nav:nth-of-type(2), .hover_nav:nth-of-type(3){
                height: 233px;

                .hover_item{
                    margin-top: 0px;
                }
            }
        }
    }
}
@media(max-width: 690px){
    .header{
        height: 54px;
        .logo{
            width: 47px;
            height: 47px;
            left: 46%;
        }
        .nav_item_container{
            display: none;
        }
        .hover_item_container{
            display: none;
        }
        .side_bar{
            .bar_btn{
                display: block;
            }
        }
    }
}

//Banner
.banner_container{
    width: 100%;
    position: relative;
    z-index: 0;
    overflow: hidden;

    .banner_repeater{
        display: flex;
        width: 100%;
        margin-left: 0%;
        transition: all 1.5s;
        
        .img_container{
            
            width: 100%;
            flex-shrink: 0;
            
            img{
                width: 100%;
                height: 100%;
                
            }
        }
    }
    .handle_carousel{
        display: flex;
        position: absolute;
        bottom: 2%;
        right: 50%;
        transform: translate(50%, 0);

        .handle_btn{
            width: 12px;
            height: 12px;
            border: solid 2px rgba(255, 255, 255, 0.219);
            border-radius: 50%;
            margin-right: 10px;
            margin-left: 10px;
            background-color: rgba(255, 255, 255, 0.055);
            cursor: pointer;
            transition: all 0.7s;

        }
    }
}

//SearchBtn
.input_container{
    display: flex;
    justify-content: center;
    align-items: center;
    
    position: absolute;
    top: 444px;
    right: 50%;
    transform: translate(50%,0);
    background-color: white;
    border-radius: 7px;
    width: 28vw;
    height: 45px;
    
    
    i{
        @include font(2);

    }
    input{
        @include font(1);

        width: 16vw;
        height: 38px;
        margin-left: 10px;
        color: $font_color;
        background-color: white;
        border: solid 1px white;
    }
    input:focus{
        border: solid 2px $logo_color;
        border-radius: 5px;
        outline: solid white 1px
    }

    .GO_a{
        .GO_btn{
            @include font(1,29px);
            font-weight: 600;
            text-align: center;
            cursor: pointer;
            width: 94px;
            height: 30px;
            background-color: $logo_color;
            border-radius: 20px;
            margin-left: 20px;
        }
        .GO_btn:hover{
            background-color: $logo_color_hover;
            
        }
    }
    .GO_a :link, :visited{
        color: $font_color;
    }
}
@media(max-width: 1430px){
    .input_container{
        top: 280px;
        width: 34vw;

        input{
            width: 18vw;
        }
    }
}
@media(max-width: 950px){
    .input_container{
        top: 150px;
        width: 52vw;

        input{
            width: 27vw;
        }
    }
}
@media(max-width: 690px){
    .input_container{
        width: 70vw;
        top: 82px;
        height: 29px;
        i{
            font-size: 16px;
        }
        input{
            width: 39vw;
            height: 27px;
            font-size: 15px;
        }
        .GO_a{
            .GO_btn{
                font-size: 16px;
                line-height: 22px;
                width: 68px;
                height: 22px;
            }
        }
    }
}
@media(max-width: 380px){
    .input_container{
        width: 79vw;
        top: 62px;
    }
}

//ActivityMain FoodMain SceneryMain
.main_container{
    width: 100%;
    max-width: 1980px;
    margin-left: auto;
    margin-right: auto;

    .first{
        position: relative;
        width: 100%;

        .main_title{
            width: 54%;
            margin-top: 100px;
            margin-bottom: 30px;
            margin-right: auto;
            margin-left: auto;
            
            @include font(4);
            
            font-weight: 550;
        }
        .main_title::before{
            content: ".";
            color: #00000000;
            
            background-color: $logo_color;
        }
        .main_items_container{
            margin-left: auto;
            margin-right: auto;
            width: 57%;

            display: flex;
            justify-content: center;
            flex-wrap: wrap;
            
        }
        .btn_container{
            width: 50%;
            height: 30px;
            margin: 20px auto 80px auto;

            display: flex;
            justify-content: center;

            .left_btn{
                @extend .btn;

            }
            .right_btn{
                @extend .left_btn;
            }
        }
    }
}
@media(max-width: 1179px){
    .main_container{
        .first{
            .main_title{
                text-align: center;
            }
            .main_items_container{
                width: 70%;
            }
        }
    }
}
@media(max-width: 690px){
    .main_container{
        .first{
            .main_title{
                text-align: center;
            }
            .main_items_container{
                width: 90%;
            }
        }
    }
}


//MainItems
.item_a{
    flex-basis: 240px;
    height: 270px;
    margin-left: 14px;
    margin-right: 14px;
    margin-bottom: 45px;
    text-align: center;
    

    .item_container{
        background-color: white;
        border-radius: 10px;

        i{
            display: inline;
        }
        img{
            width: $main_item_imgW;
            height: $main_item_imgH;
            border-radius: 10px 10px 0px 0px;
        }
        .title_container{
            
            width: 200px;
            height: 84px;
            margin-left: auto;
            margin-right: auto;

            overflow: hidden;
            

            .item_title{
                
                @include font(2);
                font-weight: 600;
                
                
            }
        }
        .caption_container{
            margin-top: 10px;

            i{
                color: $logo_color;
            }
            .item_caption{
                display: inline-block;
                margin-left: 5px;
                color: $font_color;
                font-weight: 600;
            }
        }
    }
    .item_container:hover{
        box-shadow: 1px 1px 7px 1px #c7c7c7dc;
    }
}
.item_a:visited, :link{
    color: $font_color;
    text-decoration: none;
}
@media(max-width: 1179px){
    .item_a{
        flex-basis: 0;
        height: auto;
        margin-left: 11px;
        margin-right: 11px;

        .item_container{
            img{
                width: 216px;
                height: 132px;
            }
            .title_container{
                height: 76px;
            }
        }
    }
}
@media(max-width: 690px){
    .item_a{
        flex-basis: 0;
        height: auto;
        margin-left: 4px;
        margin-right: 4px;

        .item_container{
            img{
                width: 165px;
                height: 102px;
            }
            .title_container{
                width: 167px;
                height: 55px;
                margin-top: 8px;
                .item_title{
                    font-size: 13px;
                    line-height: 22px;
                }
            }
            .caption_container{
                margin-top: 5px;
                i{
                    font-size: 6px;
                }
                .item_caption{
                    font-size: 6px;
                }
            }
        }
    }
}

//MainPageBtn    ****** 收進SCSS_ALL ******
.btn{
    font-weight: 500;

    text-align: center;
    width: 25px;
    height: 25px;

    border-radius: 50%;
    margin-left: 3px;
    margin-right: 3px;
    cursor: pointer;

    
    
}
.btn:hover{
    color: white;
    background-color: $logo_color;
}

//ScrollTop      ****** 收進SCSS_ALL ******
.to_top_btn_container{
    position: fixed;
    right: 15px;
    bottom: 10px;
    width: 45px;
    height: 45px;
    
    border-radius: 10px;
    background-color: $logo_color;

    cursor: pointer;

    .to_top_btn{
        width: 19px;
        height: 34px;
        position: absolute;
        top: 50%;
        right: 50%;
        transform: translate(50%, -50%);

        font-size: 25px;
        color: white;
        background-color: $logo_color;
    }
}
.to_top_btn_container:hover{
    opacity: 0.85;
}

//Search
.search_main{
    width: 100%;
    margin-top: 70px;

    .tips_container{
        width: 57%;
        margin-left: auto;
        margin-right: auto;
        text-align: center;
    }    
    .result_container{
        width: 100%;
        margin-left: auto;
        margin-right: auto;
    }
}

//SearchTips
.tips{
    height: 55px;
    background-color: rgb(192, 192, 192);
    border-radius: 10px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 50px;
    margin-bottom: 50px;
    display: inline-block;
    
    .container{
        text-align: center;
        
        i{
            display: inline-block;
            margin-left: 15px;
            margin-right: 5px;
        }
        p{
            display: inline-block;
            margin-right: 20px;
        }
    }
