//Footer
footer{
    width: 100%;
    height: 280px;
    background-color: #d8d8d8;
    text-align: center;
    position: relative;

    ul{
        padding: 0;
        list-style-type: none;
        position: absolute;
        top: 50px;
        left: 50%;
        transform: translate(-50%, 0);

        li{
            margin-bottom: 20px;
            @include font(1);
            color: $font_color;
        }
    }
}

//PageBanner
.page_banner_container{
    width: 100%;
    .page_banner{
        color: transparent;
        background-color: white;
        width: 100%;
        height: 11.4rem;
        background-image: url();
        background-size: 100%;
        background-repeat: no-repeat;
    }
}
@media(max-width: 650px){
    .page_banner_container{
        .page_banner{
            height: 3.4rem;
        }
    }
}

//ItemsPageMain
.main_page_container{
    width: 100%;
    height: 650px;
    position: relative;
    background-color: white;

    .page_img_container{
        width: 40%;
        display: inline-block;
        position: absolute;
        top: 100px;
        left: 10%;
        
        .page_img_big{
            width: 63%;
            margin-left: auto;
            margin-right: auto;

            img{
                width: 100%;
            }
        }
        .page_img_small{
            display: flex;
            justify-content: space-between;
            width: 63%;
            margin-left: auto;
            margin-right: auto;

            img{
                width: 32%;
                cursor: pointer;
            }
        }
    }
    .text_container{
        width: 40%;
        display: inline-block;
        position: absolute;
        top: 79px;
        right: 10%;
        
        .page_title{
            @include font(2);
            font-weight: 600;
            color: $font_color;
            margin-top: 20px;
            margin-bottom: 27px;
        }
        .page_description{
            @include font(1);
            color:$font_color;
            margin-top: 8px;
            margin-bottom: 42px;
            height: 180px;
            overflow: auto;
        }
        .open_time{
            @extend .page_description;
            width: 60%;
            height: 40px;

            i{
                color: $logo_color;
            }
            span{
                margin-left: 4px;
            }
        }
        .position{
            display: flex;
            .address{
                @extend .page_description;
                margin-right: 100px;
                height: 40px;

                i{
                    color: $logo_color;
                }
            }
            .google_map{
                @extend .page_description;
                font-weight: 600;
                height: 40px;
                i{
                    color: $logo_color;
                }
                a:visited{
                    color: $font_color;
                    
                    i{
                        color: $logo_color;
                    }
                }
                a:hover{
                    color: black;
                }
            }
        }
        .website{
            @extend .page_description;
            height: 20px;
            a{

                i{
                    color: $logo_color;
                }
            }
            a:visited{
                color: $font_color;
                
                i{
                    color: $logo_color;
                }
            }
            a:hover{
                color: black;
            }
        }
        .phone{
            @extend .page_description;
            height: 20px;
            i{
                color: $logo_color;
            }
        }
    }
}
@media(max-width: 650px){
    .main_page_container{
        padding-top: 20px;
        padding-bottom: 20px;
        height: 830px;
        .page_img_container{
            width: 100%;
            position: relative;
            top: 0;
            left: 0;
        }
        .text_container{
            width: 100%;
            position: relative;
            top: 0;
            right: 0;
            .page_title{
                text-align: center;
            }
            .page_description{
                margin-left: 15px;
                margin-right: 15px;
            }
            .open_time{
                width: 80%;
                text-align: center;
                margin-left: auto;
                margin-right: auto;
            }
            .position{
                width: 97%;
                margin-left: auto;
                margin-right: auto;
                justify-content: center;
                .address{
                    margin-right: 19%;
                }
            }
            .website{
                text-align: center;
            }
            .phone{
                text-align: center;
            }
        }
    }
}

//ItemsPageCarousel
.page_carousel_container{
    width: 100%;
    height: 530px;
    display: flex;
    flex-wrap: wrap;
    .carousel_title{
        width: 100%;
        text-align: center;
        @include font(2);
        font-weight: 800;
        margin-top: 75px;
        i{
            color: $logo_color;
        }
    }
    .carousel_repeater{
        width: 65%;
        height: 290px;
        margin-top: 55px;
        overflow: hidden;
        .item_container{
            width: 65%;
            height: 290px;
            margin-left: 5px;
            display: flex;
            flex-wrap: nowrap;
            transition: all 1.3s;
            
        //CarouselItems
            .item{
                width: 230px;
                height: 265px;
                margin-top: 5px;
                background-image: url();
                background-repeat: no-repeat;
                background-size: 100% 100%;
                border-radius: 10px;
                margin-left: 8px;
                margin-right: 7px;
                display: flex;
                flex-direction: column;
                justify-content: end;
                transition: all 1s;

                .item_name{
                    text-align: center;
                    font-size: 16px;
                    font-weight: 600;
                    color: white;
                    margin-bottom: 13px;
                }
                .item_open_time{
                    text-align: center;
                    color: white;
                    font-size: 8px;
                    margin-bottom: 15px;
                }
            }
            .item:hover{
                background-size: 110% 110%;
            }
        }
    }
    .carousel_btn{
        width: 17.5%;
        position: relative;
        .btn_container{
            width: 40px;
            height: 75px;
            background-color: $logo_color;
            border-radius: 4px;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            cursor: pointer;
            i{
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-45%, -50%);
                font-size: 40px;
                color: white;
            }
        }
        .btn_container:hover{
            background-color: $logo_color_hover;
        }
    }
}
@media(max-width: 1179px){
    .page_carousel_container{
        width: 90%;
    }
}



